import { InjectedConnector } from '@web3-react/injected-connector';
import { WalletConnectConnector } from '@web3-react/walletconnect-connector';
import { WalletLinkConnector } from '@web3-react/walletlink-connector';
import { TrezorConnector } from '@web3-react/trezor-connector';
import { LedgerConnector } from '@web3-react/ledger-connector';
import { LatticeConnector } from '@web3-react/lattice-connector'

const POLLING_INTERVAL = 12000

export const injected = new InjectedConnector({
  supportedChainIds: [1, 3, 4, 5, 42, 56, 137, 97, 56, 1337, 43114]
});

export const walletconnect = new WalletConnectConnector({
  rpc: { 1: process.env.REACT_APP_RPC_URL_1 },
  qrcode: true
});

export const walletlink = new WalletLinkConnector({
  url: process.env.RPC_URL_1,
  appName: 'web3-react example',
  supportedChainIds: [1, 3, 4, 5, 42, 10, 137, 43114, 69, 420, 80001]
});

export const ledger = new LedgerConnector({
  chainId: 1,
  url: process.env.REACT_APP_RPC_URL_1,
  pollingInterval: POLLING_INTERVAL
});

export const trezor = new TrezorConnector({
  chainId: 1,
  url: process.env.REACT_APP_RPC_URL_1,
  pollingInterval: POLLING_INTERVAL,
  manifestEmail: 'dummy@abc.xyz',
  manifestAppUrl: 'http://localhost:1234'
});

export const lattice = new LatticeConnector({
  chainId: 4,
  appName: 'web3-react',
  url: process.env.REACT_APP_RPC_URL_4
})

export const web3wallet = new InjectedConnector({
  supportedChainIds: [1, 97, 56, 1337]
});
