import React from "react";
import { InputGroup, FormControl, Button } from 'react-bootstrap';

import Blur3 from 'assets/svg/blur-point-3.svg';
import Fow2 from 'assets/svg/fow-2.svg';
import Access from 'assets/image/access.png';
import Refer1 from 'assets/image/refer-bgr-1.png';
import Refer2 from 'assets/image/refer-bgr-2.png';
const SignAndRefer = () => {
  return (
    <>
      <div className='SignUp'>
        <div>
          <div className='TextTitle TrickleTitle'>
            <strong>Get Access to Trickle</strong>
          </div>
          <p className='TextContent mt-2'>Sign up to be notified about the closed beta!</p>
          <p className='SubTitle'>Join our waitlist:</p>
          <img src={Blur3} alt="Blur" className='Blur3' />
          <img src={Fow2} alt="Fow" className='Fow2' />
        </div>
        <div className='d-flex'>
          <InputGroup className="SignInputGroup mb-3">
            <FormControl
              placeholder="Enter Your Email Address"
              aria-label="Enter Your Email Address"
              aria-describedby="basic-addon2"
              className="SignInput"
            />
            <Button variant="outline-secondary" className='SignBtn'>
              Sign Up
            </Button>
          </InputGroup>
          <img src={Access} alt="Access" className='Access' />
        </div>
      </div>
      <div className='Refer d-flex flex-column align-items-center'>
        <div className='TextTitle'>
          <strong>
            Refer friends
          </strong>
        </div>
        <div className='TextContent text-center'>
          Earn rewards and gain access to our exclusive closed beta app <br /> release by signing up and inviting your friends!
        </div>
        <Button className='PublicBtn mt-4' onClick={() => window.open("https://earn.trickle.cloud")}>
          Refer
        </Button>
        <img src={Refer1} alt="Refer" className='Refer1' />
        <img src={Refer2} alt="Refer" className='Refer2' />
        {/* <img src={Refer3} alt="Refer" className='Refer3' /> */}
      </div>
    </>
  )
}

export default SignAndRefer