import React, { useState } from "react";
import { Nav, Navbar, Container, Button } from "react-bootstrap";
import { useWeb3React } from "@web3-react/core";
import WalletModal from "Modal/walletModal";
import logoImage from "assets/svg/logo.svg";
import BeforeLogin from "./beforeLogin";
import AfterLogin from "./afterLogin";
import "./index.scss";

const Header = ({ connectionStatus, onInstallMetaModalShow, setAccount, setBalance, setLoginModalShow, setConnectionStatus }) => {
  const { account, active } = useWeb3React();
  const [walletModalShow, setWalletModalShow] = useState(false);
  const handleWallet = () => {
    setWalletModalShow(true);
  };
  return (
    <div className="Header">
      <Navbar collapseOnSelect bg="transparent" variant="dark" className="NavContainer">
        <Container className="max-w-screen-lg">
          <Navbar.Collapse id="responsive-navbar-nav">
            <Navbar.Brand href="/">
              <img src={logoImage} alt="logo" />
            </Navbar.Brand>
            <div className="m-auto"></div>
            {
              window.location.href.includes('deposit') ?
                <Nav>
                  {
                    active ?
                      <AfterLogin account={account} /> :
                      <BeforeLogin handleWallet={handleWallet} />
                  }
                </Nav> :
                <Nav>
                    <Button className="NavBtn" onClick={() => window.open("https://docsend.com/view/x2k8x2w23enr3zsy")}>Pitch Deck</Button>
                    <Button className="NavBtn" onClick={() => window.open("https://docsend.com/view/i5j6k9tyxebrdvzi")}>Litepaper</Button>
                    <Button className="PublicBtn">Sign Up</Button>
                </Nav>
            }
            {/* <Nav className="m-auto NavLinkContainer">
              <Nav.Link className="NavLink mr-1" href="#Deposit">
                Deposit
              </Nav.Link>
              <Nav.Link className="NavLink mx-1" href="#Prizes">
                Prizes
              </Nav.Link>
              <Nav.Link className="NavLink ml-1" href="#Account">
                Account
              </Nav.Link>
            </Nav> */}
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <WalletModal
        show={walletModalShow}
        connectionStatus={connectionStatus}
        onHide={() => setWalletModalShow(false)}
        setAccount={setAccount}
        setBalance={setBalance}
        setLoginModalShow={setLoginModalShow}
        setConnectionStatus={setConnectionStatus}
        onInstallMetaModalShow={onInstallMetaModalShow}
      />
    </div>
  );
};
export default Header;
