import React, { useEffect } from "react";
import { CloseButton, Modal, Button, Spinner } from "react-bootstrap";
import "./index.scss";

const LoginAndAuthorizeModal = ({ show, onHide, connectionStatus }) => {
  useEffect(() => {
    connectionStatus === 2 && onHide();
  }, [connectionStatus, onHide]);

  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="contained-modal-title-vcenter"
      className="LoginModal"
      centered
    >
      <Modal.Header className="border-0">
        <div className="ModalTitle">
          <Modal.Title>Login and Authorize Your Wallet</Modal.Title>
        </div>
        <CloseButton variant="white" onClick={onHide} />
      </Modal.Header>
      <Modal.Body>
        <p className="ModalContent">
          Trickle requires access to your wallet, please login and authorize access to your WalletConnect account to continue.
        </p>{console.log("connectionstatus:", connectionStatus)}
        {connectionStatus === 1 && <div className="Alert mb-2">This dapp requires access to your account information.</div>}
        <div className="d-flex align-items-center">
          <Button className="DismissBtn" onClick={onHide}>Dismiss</Button>
          {connectionStatus !== 1 && <Spinner animation="border" variant="light" className="Spinner" />}
        </div>
      </Modal.Body>
    </Modal>
  );
};
export default LoginAndAuthorizeModal;
