import React, { useState, useEffect } from "react";
import { Modal, CloseButton, Spinner } from "react-bootstrap";
//-----------------------Web3------------------------
import { useWeb3React } from "@web3-react/core";
import { injected, walletconnect, walletlink, ledger, trezor, lattice, web3wallet } from "Connector";

import * as RequestService from 'Service/RequestService';
import metamaskIcon from 'assets/image/metamask.png';
import walletConnectIcon from 'assets/image/walletconnect.png';
import coinbaseIcon from 'assets/image/coinbase.png';
import trezorIcon from 'assets/image/trezor.png';
import ledgerIcon from 'assets/image/ledger.png';
import latticeIcon from 'assets/svg/lattice.svg';
import web3Icon from 'assets/image/web3wallet.png';

import "./index.scss";

const walletInfo = [
	{ title: 'MetaMask', logo: <img src={metamaskIcon} alt="metamask" width="20" />, connector: injected },
	{ title: 'WalletConnect', logo: <img src={walletConnectIcon} alt="walletconnect" width="20" />, connector: walletconnect },
	{ title: 'Coinbase Wallet', logo: <img src={coinbaseIcon} alt="coinbase" width="20" />, connector: walletlink },
	{ title: 'Trezor', logo: <img src={trezorIcon} alt="trezor" width="30" />, connector: trezor },
	{ title: 'Ledger', logo: <img src={ledgerIcon} alt="ledger" width="30" />, connector: ledger },
	{ title: 'Lattice', logo: <img src={latticeIcon} alt="lattice" width="20" />, connector: lattice },
	{ title: 'Web3 Wallet', logo: <img src={web3Icon} alt="web3" width="20" />, connector: web3wallet }
]

const Wallet = ({ show, onHide, setAccount, setBalance, setLoginModalShow, setConnectionStatus, onInstallMetaModalShow }) => {
	const [descriptionShow, setDescriptionShow] = useState(false);
	const [activatingConnector, setActivatingConnector] = useState();
	const { account, connector, activate, library, active } = useWeb3React();
	const handleDescription = () => {
		setDescriptionShow(!descriptionShow);
	};
	const handleHideAndLogin = () => {
		onHide();
		setLoginModalShow(true);
	}
	useEffect(() => {
		if (activatingConnector && activatingConnector === connector) {
			setActivatingConnector(undefined)
		}
	}, [activatingConnector, connector]);

	useEffect(() => {
		const getBal = async () => {
			try {
				const balance = await library.eth.getBalance(account);
				setBalance(library.utils.fromWei(balance, 'ether'));
			} catch (e) {
				console.error("No Balance: ", e);
			}
		}
		if (active) {
			setAccount && setAccount(account);
			getBal();
		}
	}, [account, setAccount, setBalance, library, active]);

	return (
		<Modal
			show={show}
			onHide={onHide}
			aria-labelledby="contained-modal-title-vcenter"
			className="WalletModal"
			centered
		>
			<Modal.Header className="border-0">
				<Modal.Title className="ModalTitle h6">Select a Wallet</Modal.Title>
				<CloseButton className="ModalCloseBtn" variant="white" onClick={onHide}></CloseButton>
			</Modal.Header>
			<Modal.Body className="show-grid flex-column d-flex justify-content-md-center mt-1 mb-1">
				<p>
					By connecting a wallet, you agree to Trickle's{" "}
					<a href="#Term">Terms of Service</a> and acknowledge that you have
					read and understand the{" "}
					<a href="protocol">Trickle protocol disclaimer</a>.
				</p>
				{
					walletInfo.map((element, index) => {
						const activating = element.connector === activatingConnector;
						return (
							<div
								key={index}
								className="Wallet d-flex mx-5 my-1 justify-content-between"
								onClick={() => {
									setActivatingConnector(element.connector);
									RequestService.connect(element.connector, onInstallMetaModalShow, activate, setActivatingConnector, setConnectionStatus);
									handleHideAndLogin();
								}}
							>
								<div>{element.title}</div>
								{activating ?
									<Spinner animation="border" variant="light" /> : <div>{element.logo}</div>}
							</div>
						)
					})
				}
			</Modal.Body>
			<Modal.Footer className="d-flex align-items-start flex-column border-0 mb-4">
				<span className="Description" onClick={handleDescription}>What is a wallet?</span>
				{descriptionShow && (
					<div>
						Wallets are used to send, receive, and store digital assets like
						Ether. Wallets come in many forms. They are either built into your
						browser, an extension added to your browser, a piece of hardware
						plugged into your computer or even an app on your phone. For more
						information about wallets, see this explanation.
					</div>
				)}
			</Modal.Footer>
		</Modal>
	);
};
export default Wallet