import React from "react";
import { CloseButton, Modal, Button } from "react-bootstrap";
import metaIcon from 'assets/image/metamask.png';
import "./index.scss";

const InstallMetaModal = ({ show, onHide }) => {
  return (
    <Modal
      show={show}
      onHide={onHide}
      className="InstallMetaModal"
      aria-labelledby="contained-modal-title-vcenter"
      centered
    >
      <Modal.Header className="border-0">
        <div className="mt-3">
          <Modal.Title className="h6">Select a Wallet</Modal.Title>
        </div>
        <CloseButton variant="white" onClick={onHide} />
      </Modal.Header>
      <Modal.Body className="show-grid d-flex flex-column justify-content-md-center mt-1 mb-1 px-4">
        <div className="d-flex align-items-center">
          <img src={metaIcon} width={60} alt="Metamask" />
          <div className="h5 m-0 ms-3">MetaMask</div>
        </div>
        <p className="w-100 px-2 mt-5 text-center">
          You'll need to open or install MetaMask to continue.
        </p>
      </Modal.Body>
      <Modal.Footer className="d-flex border-0 justify-content-between">
        <Button className="BtnBack" onClick={onHide}>Back</Button>
        <Button className="BtnInstall" onClick={() => window.open("https://metamask.io/download")}>Open or install MetaMask</Button>
      </Modal.Footer>
    </Modal>
  );
};
export default InstallMetaModal;
