import React from 'react';
import FeatureIcon1 from 'assets/image/features-icon1.png';
import FeatureIcon2 from 'assets/image/features-icon2.png';
import FeatureIcon3 from 'assets/image/features-icon3.png';
import FeatureIcon4 from 'assets/image/features-icon4.png';
import Blur1 from 'assets/svg/blur-point-1.svg';
import Blur2 from 'assets/svg/blur-point-2.svg';
import Fow1 from 'assets/svg/fow-1.svg';

const Features = () => {
  const data = [
    { img: FeatureIcon1, title: "Simply User Friendly", content: "We’ve taken the hassle out of coding and developed \n a dashboard UI for step by step deployment and \n participation. Create and deploy your dApps with \n ease." },
    { img: FeatureIcon2, title: "Smart Contract Templates", content: "Take any audited smart contract and customize it to\n your needs without ever seeing a line of code. Never \n have to worry about debugging broken code again!" },
    { img: FeatureIcon3, title: "Grow Your Audience", content: "Share your dApp on Trickle and gain exposure to \n thousands of users at the click of a button. Grow \n your userbase and earn rewards as it flourishes." },
    { img: FeatureIcon4, title: "Unlimited Potential", content: "Our archive is like any databank, over time the \n open-source resources will grow and so too will the \n available dApps you can deploy on Trickle." }
  ]
  return (
    <>
      <div className='Features'>
        <div className='ContentLeft'>
          <div className='SubContent'>
            <div className='TextTitle FeatureTextTitle mb-4'>
              <strong>Our Key <span className='Important'>Features</span></strong>
            </div>
            <p className='TextContent FeatureTextContent'>
              Trickle combines simplicity and functionality to bring you
              <br />one of the most user friendly feature rich tools you will
              <br />find in the Crypto Space. Built by developers, for
              <br />everyone.
            </p>
          </div>

          <div className='Video'>
            <video className="FeatureVideo" autoPlay muted loop>
              <source src="video/feature.mp4" type="video/mp4"></source>
            </video>
          </div>
        </div>
        <div className='ContentRight'>
          {data.map((e, i) =>
            <div className='TextContainer d-flex align-items-center' key={i}>
              <img src={e.img} className='ImgFeature' alt={`feature${i}`} />
              <div className='TextSubContainer'>
                <p className='Title'>{e.title}</p>
                <p className='TextContent'>{e.content}</p>
                {/* {e.content.split('\n').map((content, index) =>
                  <p className='TextContent mt-0 mb-0' key={index}>{content}</p>
                )} */}
              </div>
            </div>
          )}
          <img src={Blur1} alt="BlurSVG" className="Blur1" />
          <img src={Fow1} alt="FowSVG" className='Fow1' />
        </div>
      </div>
      <img src={Blur2} className="Blur2" alt="BlurSVG" />
    </>
  );
}
export default Features