import React from "react";
import { Nav } from 'react-bootstrap';
import Logo from 'assets/svg/logo.svg';

const Footer = () => {
  return (
    <>
      <div className='Footer'>
        <div className='FooterContainer'>
          <div className='FooterLogo'>
            <img src={Logo} alt='logo' className='LogoImg' />
            <p className='LogoContent mt-4'>Trickle is an open-source smart contract archive.<br />
              Designed for integration with Substrate in mind.</p>
          </div>
          <div className='FooterLink d-flex'>
            <div className='d-flex flex-column align-items-start'>
              <p className='LinkTitle'>General</p>
              <Nav.Link className='LinkContent' href="https://docsend.com/view/x2k8x2w23enr3zsy">Pitch Deck</Nav.Link>
              <Nav.Link className='LinkContent' href="https://docs.google.com/forms/d/e/1FAIpQLSdNmNb2Mze8nG43-fMGcjIHrmIDCoWfMVmMi8B-gBk7V7qEFg/viewform">Contact</Nav.Link>
            </div>
            <div className='FooterSubContainer d-flex flex-column align-items-start'>
              <p className='LinkTitle'>Resource</p>
              <Nav.Link className='LinkContent' href="https://ignition.paidnetwork.com/project/trickle">Public Sale</Nav.Link>
              <Nav.Link className='LinkContent' href="https://docsend.com/view/i5j6k9tyxebrdvzi">Litepaper</Nav.Link>
              <Nav.Link className='LinkContent' href="#">Go to the app</Nav.Link>
              <Nav.Link className='LinkContent' href="#">Developers</Nav.Link>
            </div>
            <div className='FooterSubContainer d-flex flex-column align-items-start'>
              <p className='LinkTitle'>Follow Us</p>
              <Nav.Link className='LinkContent' href="https://twitter.com/trickledrip">Twitter</Nav.Link>
              <Nav.Link className='LinkContent' href="https://blog.trickle.cloud">Medium</Nav.Link>
              <Nav.Link className='LinkContent' href="https://t.me/trickledrip">Telegram</Nav.Link>
              <Nav.Link className='LinkContent' href="https://coinmarketcap.com/currencies/trickle/ico/">CoinMarketCap</Nav.Link>
            </div>
          </div>
        </div>
        <div className='FooterCopyRight'>
          <p>© 2021 Trickle Ltd. All Rights Reserved</p>
        </div>
      </div>
    </>
  )
}
export default Footer;