import React from "react";
import { CloseButton, Modal, Button } from "react-bootstrap";
import polygonIcon from "assets/image/polygon.png";
import etherIcon from "assets/image/ethereum.png";
import "./index.scss";

const SelectDepoit = ({ show, onHide, setType }) => {
  const EtherSVG = ({ size }) => (
    <img src={etherIcon} width={size} height={size} alt="Ether" />
  );

  const PolygonSVG = ({ size }) => (
    <img src={polygonIcon} width={size} height={size} alt="Polygon" />
  );

  const handleEther = () => {
    setType(0);
    onHide();
  }

  const handlePolygon = () => {
    setType(1);
    onHide();
  }
  
  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="contained-modal-title-vcenter"
      className="DepositModal"
      centered
    >
      <Modal.Header className="border-0">
        <div className="ModalTitle mt-3">
          <Modal.Title>CHOOSE A NETWORK</Modal.Title>
        </div>
        <CloseButton variant="white" onClick={onHide}/>
      </Modal.Header>
      <Modal.Body className="show-grid d-flex justify-content-md-center mt-1 mb-1 px-4">
        <p className="w-100 px-5 text-center">
          All networks deposit into the same prize pool and have the same chances of winning prizes. Pick your favourite and good luck!
        </p>
      </Modal.Body>
      <Modal.Footer className="ModalFooter d-flex flex-column border-0">
        <Button className="ModalBtn" onClick={handleEther}><PolygonSVG size={20} /> Polygon</Button>
        <Button className="ModalBtn" onClick={handlePolygon}><EtherSVG size={20} /> Ethereum</Button>
      </Modal.Footer>
    </Modal>
  );
};
export default SelectDepoit;
