import React, { useEffect, useState } from "react";
import { Button, } from "react-bootstrap";
import { useWeb3React } from "@web3-react/core";
import CountUp from 'react-countup';
import { CgArrowsH } from "react-icons/cg";
import { BsArrowLeft, BsPlusCircle, BsQuestionCircle, BsFillSquareFill, BsCircleFill } from "react-icons/bs";
import { IoIosArrowDown } from 'react-icons/io';

//------import components-------
import Header from 'Components/head';
import Timer from "Components/timer";

//-------import Modal---------
import PrizeModal from "Modal/prizeModal";
import BridgeModal from "Modal/bridgeModal";
import WalletModal from "Modal/walletModal";
import DepoitModal from "Modal/depositModal";
import InstallMetaModal from 'Modal/walletModal/installMetaModal';
import LoginAndAuthorizeModal from 'Modal/walletModal/loginAndAuthorize';

//-------import Assets----------
import etherIcon from "assets/image/ethereum.png";
import polygonIcon from "assets/image/polygon.png";
// import blueIcon from 'assets/image/lightning-blue.png';
// import yellowIcon from 'assets/image/lightning-yellow.png';
import referIcon1 from 'assets/image/refer-bgr-1.png';
import referIcon2 from 'assets/image/refer-bgr-2.png';
import feature from 'assets/image/features-icon2.png';
import usdcIcon from "assets/svg/usdc.svg";
import walletIcon from 'assets/svg/wallet.svg';
import "./index.scss";

const EtherSVG = ({ size }) => (
	<img src={etherIcon} width={size} height={size} alt="Ether" />
);

const PolygonSVG = ({ size }) => (
	<img src={polygonIcon} width={size} height={size} alt="Polygon" />
);

const UsdcSVG = ({ size }) => (
	<img src={usdcIcon} width={size} height={size} alt="USDC" />
);

const WalletSVG = ({ size }) => (
	<img src={walletIcon} width={size} height={size} alt="Wallet" />
);

const Deposit = () => {
	const [type, setType] = useState(0);
	const { active } = useWeb3React();
	const [prizeModalShow, setPrizeModalShow] = useState(false);
	const [bridgeModalShow, setBridgeModalShow] = useState(false);
	const [depositModalShow, setDepositModalShow] = useState(false);
	const [walletModalShow, setWalletModalShow] = useState(false);
	const [installMetaModalShow, setInstallMetaModalShow] = useState(false);
	const [loginModalShow, setLoginModalShow] = useState(false);

	const [account, setAccount] = useState('');
	const [balance, setBalance] = useState();
	const [amount, setAmount] = useState(0.0);
	const [isNumber, setNumberCheck] = useState(false);
	const [connectionStatus, setConnectionStatus] = useState(0); // 0-not connected 1-connecting 2-connected
	const [flag, setFlag] = useState(false);

	useEffect(() => {
		setTimeout(() => {
			setFlag(true);
		}, 1000);
	}, [])

	const handleWallet = () => {
		setWalletModalShow(true);
	};

	const handleAmount = (e) => {
		if (e.target.validationMessage) {
			setAmount(e.target.value);
			setNumberCheck(true);
		} else {
			setNumberCheck(false);
		}
	};

	const handleDepositBtn = () => {
		setDepositModalShow(true)
	}

	return (
		<>
			<Header
				onInstallMetaModalShow={() => setInstallMetaModalShow(true)}
				setAccount={setAccount}
				setBalance={setBalance}
				setLoginModalShow={setLoginModalShow}
				setConnectionStatus={setConnectionStatus}
			/>
			<div className="Deposit">
				<div>
					<div className="InnerCard">
						<span className="PrizeEveryDay">
							<button className="PrizeLink" onClick={() => setPrizeModalShow(true)}>4096</button> PRIZES. EVERY. SINGLE. DAY.
						</span>
						<div className="Balance">${
							flag ?
								<CountUp start={0.0} end={17632} duration={2} delay={0} separator="," /> :
								<span>0.00</span>
						}</div>
						<div className="DrawClose">JOIN DRAW #65</div>
						<Timer />
						<div className="d-none d-sm-block">
							<img src={referIcon1} alt="Lightning-blue" className="BlueLeft" />
							<img src={referIcon2} alt="Lightning-yellow" className="YellowLeft" />
							<img src={referIcon1} alt="Lightning-blue" className="BlueRight" />
							<img src={referIcon2} alt="Lightning-yellow" className="YellowRight" />
							<img src={feature} alt = "feature" className="SIconFirst"/>
							<img src={feature} alt = "feature" className="SIconSecond"/>
							<img src={feature} alt = "feature" className="SIconThird"/>
							<img src={feature} alt = "feature" className="CIconFirst"/>
							<img src={feature} alt = "feature" className="CIconSecond"/>
							<img src={feature} alt = "feature" className="CIconThird"/>
							{/* <img src={blueIcon} alt="Lightning-blue" className="BlueLeft" />
							<img src={yellowIcon} alt="Lightning-yellow" className="YellowLeft" />
							<img src={blueIcon} alt="Lightning-blue" className="BlueRight" />
							<img src={yellowIcon} alt="Lightning-yellow" className="YellowRight" /> */}
							{/* <BsFillSquareFill className="SIconFirst" size={8} />
							<BsFillSquareFill className="SIconSecond" size={8} />
							<BsFillSquareFill className="SIconThird" size={8} />
							<BsCircleFill className="CIconFirst" size={8} />
							<BsCircleFill className="CIconSecond" size={8} />
							<BsCircleFill className="CIconThird" size={8} /> */}
						</div>

					</div>
				</div>
				<div className="Card mt-3">
					<div className="DepositUSDC d-flex align-items-center">
						Deposit on &nbsp;
						<Button className="DropBtnContainer" variant="light" onClick={handleDepositBtn}>{
							type ?
								<div className="DropBtnText d-flex align-items-center"><EtherSVG size={19} />&nbsp;Ethereum&nbsp;<IoIosArrowDown size={25} /></div> :
								<div className="DropBtnText d-flex align-items-center"><PolygonSVG size={19} />&nbsp;Polygon&nbsp;<IoIosArrowDown size={25} /></div>}
						</Button>
					</div>
					<div className="d-flex justify-content-between">
						<div className="Amount">AMOUNT</div>
						{active &&
							<div className="Amount align-items-center">
								<WalletSVG size={20} />&nbsp;
								{Math.floor(balance * 100) / 100}
								&nbsp;USDC
							</div>}
					</div>
					<div className="position-relative">
						<div className="USDCInput align-items-center d-flex position-absolute">
							<UsdcSVG size={35} />
							USDC
						</div>
						<input
							type="text"
							placeholder="0.0"
							className="InputValue"
							onChange={handleAmount}
							pattern="^(\d*\.)?\d+$"
						/>
						{isNumber ? (
							<div className="Issue">
								<div>Issues:</div>
								<div className="text-danger">Deposit amount required</div>
							</div>
						) : (
							<div className="NetworkFee">
								<div className="Estimate">Estimated network fees:</div>
								<div className="text-white">$10</div>
							</div>
						)}
					</div>
					<div className="ConnectButton" onClick={handleWallet}>Connect Wallet</div>
				</div>
				<div className="Support mt-1 py-3">
					<div
						className="SupportBtn col-sm-4"
						onClick={() => setBridgeModalShow(true)}
					>
						<CgArrowsH size={14} /> Bridge tokens
					</div>
					<div className="SupportBtnHelp col-sm-4">
						{/* <a href="https://docs.pooltogether.com/how-to/how-to-deposit"> */}
						<a href="https://docsend.com/view/i5j6k9tyxebrdvzi">
							<BsQuestionCircle size={14} /> Help
						</a>
					</div>
					<div className="SupportBtn col-sm-4">
						<BsPlusCircle size={14} className="mr-3" /> Get tokens
					</div>
				</div>
				{/* <div className="PreviousPool mt-3 mb-1 p-2">
					This is PoolTogether V4, Previous pools are on V3.
				</div>
				<button className="BackTo">
					<BsArrowLeft size={14} /> Back to V3
				</button> */}
				<div>
					<PrizeModal
						show={prizeModalShow}
						onHide={() => setPrizeModalShow(false)}
					/>
					<BridgeModal
						show={bridgeModalShow}
						onHide={() => setBridgeModalShow(false)}
					/>
					<DepoitModal
						show={depositModalShow}
						onHide={() => setDepositModalShow(false)}
						setType={setType}
					/>
					<WalletModal
						show={walletModalShow}
						onHide={() => setWalletModalShow(false)}
						onInstallMetaModalShow={() => setInstallMetaModalShow(true)}
						setAccount={setAccount}
						setBalance={setBalance}
						setLoginModalShow={setLoginModalShow}
						setConnectionStatus={setConnectionStatus}
					/>
					<InstallMetaModal
						show={installMetaModalShow}
						onHide={() => setInstallMetaModalShow(false)}
					/>
					<LoginAndAuthorizeModal
						show={loginModalShow}
						onHide={() => {
							setLoginModalShow(false);
							setConnectionStatus(0);
						}}
						connectionStatus={connectionStatus}
					/>
				</div>
			</div>
		</>
	);
};

export default Deposit;
