import React, { useEffect, useState } from "react";
import './index.scss';

const Timer = () => {
  const currentTime = new Date().getTime();
  const [distance, setDistance] = useState(
    new Date("Dec 25, 2022 22:55:00").getTime() - currentTime
  );
  const [day, setDay] = useState(Math.floor(distance / (1000 * 60 * 60 * 24)));
  const [hour, setHour] = useState(
    Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
  );
  const [min, setMin] = useState(
    Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60))
  );
  const [sec, setSec] = useState(Math.floor((distance % (1000 * 60)) / 1000));
  useEffect(() => {
    setTimeout(() => {
      setDistance(distance - 1000);
      setDay(Math.floor(distance / (1000 * 60 * 60 * 24)));
      setHour(
        Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
      );
      setMin(Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)));
      setSec(Math.floor((distance % (1000 * 60)) / 1000));
    }, 1000);
  }, [distance, setDistance]);
  return (
    <div>
      {
        <div className="d-flex justify-content-center mb-2 font-semibold">
          {
            // parseInt(day) > 0 && <div className="mx-1">
            //   <div className="d-flex">
            //     <div className="TimerContent">{Math.floor(day / 10)}</div>
            //     <div className="TimerContent">{day % 10}</div>
            //   </div>
            //   <span className="TimerSpan">DAY</span>
            // </div>
          }
          <div className="mr-1">
            <div className="d-flex">
              <div className="TimerContent">{Math.floor(hour / 10)}</div>
              <div className="TimerContent">{hour % 10}</div>
            </div>
            <span className='TimerSpan'>HR</span>
          </div>
          <div className="colorOrange">:</div>
          <div className="mx-1">
            <div className="d-flex ">
              <div className="TimerContent">{Math.floor(min / 10)}</div>
              <div className="TimerContent">{min % 10}</div>
            </div>
            <span className='TimerSpan'>MIN</span>
          </div>
          <div className="colorOrange">:</div>
          <div className="ml-1">
            <div className="d-flex">
              <div className="TimerContent">{Math.floor(sec / 10)}</div>
              <div className="TimerContent">{sec % 10}</div></div>
            <span className='TimerSpan'>SEC</span>
          </div>
        </div>
      }
    </div>
  );
};
export default Timer;
