import React from "react";
import { CloseButton, Modal, Button } from "react-bootstrap";
import { useWeb3React } from "@web3-react/core";
import * as WalletService from 'Service/WalletService';
import * as RequestService from 'Service/RequestService';
import "./index.scss";

const ChooseNetwork = ({ show, onHide, setNetwork, networkData }) => {
  const { library } = useWeb3React();
  const { networkName } = WalletService.getNetworkNameAndURL(library);
  const handleNetwork = (val) => {
    RequestService.requestChangeNetwork(val)
    setNetwork(val);
    onHide();
  }

  return (
    <Modal
      show={show}
      onHide={onHide}
      aria-labelledby="contained-modal-title-vcenter"
      className="ChooseNet"
      centered
    >
      <Modal.Header className="border-0 align-items-start">
        <p className="mt-4 h5">CHOOSE A NETWORK</p>
        <CloseButton variant="white" className="CloseBtn" onClick={onHide}/>
      </Modal.Header>
      <Modal.Body className="d-flex px-3 py-0">

        <p className="w-100">
          Select a network to switch to in MetaMask.
        </p>
        {/* <p className="w-100 px-5 text-center">
          Please switch to a supported network
        </p> */}
      </Modal.Body>
      <Modal.Footer className="d-flex flex-column border-0">{
        networkData.map((element, index) =>
          <Button className="SelectBtn" key={index} onClick={() => handleNetwork(element.chainId)}>{element.logo} {element.name}</Button>
        )
      }
        <div className="FooterText">Currently connected to: {networkName}</div>
      </Modal.Footer>
    </Modal>
  );
};
export default ChooseNetwork;
